.intro {
  background-color: #FFF3E3;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.intro-window {
  background-color: #FFFFFF;
  min-width: 75vw;
  height: 40vh;
  border: 1px solid #000000;
}

.intro-header {
  background-color: #589CF6;
  height: 7.5vh;
  border-bottom: #000000;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  font-family: Metropolis-SemiBoldItalic;
  font-size: 3vmin;
  color: #ffffff;
  padding-left: 20px;
}

.heading {
    font-family: Metropolis-Black;
    font-size: calc(14px + 5.5vmin);
    color: #373737;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 32.5vh;
    padding-left: 20px;
    padding-right: 20px;
    letter-spacing: -0.5px;
    align-items: center;
}

.button {
  border-radius: 12px;
  border-width: 0;
  height: 7vmin;
  font-family: Metropolis-Black;
  font-size: 3.5vmin;
  text-align: center;
  cursor: pointer;
}

.button-text {
  align-items: center;
  justify-content: center;
  vertical-align: center;
  line-height: 4.5vmin;
  color: white;
  padding-left: 0.25vmin;
  padding-right: 0.25vmin;
  display: inline-block;
}

.button-text-two {
  align-items: center;
  justify-content: center;
  vertical-align: center;
  line-height: 4.5vmin;
  color: white;
  padding-left: 0.25vmin;
  padding-right: 0.25vmin;
  padding-top: 0.375vmin;
  display: inline-block;
}

.window-icon {
  height: 4vmin;
  width: 4vmin;
  border-radius: 8px;
  border: 1px white solid;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 3vmin;
  font-family: Metropolis-SemiBold;
}

.exit {
  background-color: #FD6767;
  margin-right: 5px
}

.minimize {
  background-color: #E8CC58;
  margin-right: 5px
}

.full-screen {
  background-color: #5DDBB1;
}

.flex-row {
  display: flex;
  flex-direction: row;
}
.github {
  background-color: #FD6767;
  width: 23vmin;
}

.icon {
  width: 4.5vmin;
  height: 4.5vmin;
  float: left;
  padding-left: 0.625vmin;
  padding-right: 0.25vmin;
}

.intro-text {
  padding: 1vmin;
}

.line-break {
  height: 1.5vmin;

}
.linkedin{
  background-color: #589CF6;
  width: 25.25vmin;
}

.padding-horizontal {
  padding-left: 5vmin;
  padding-right: 5vmin;
}

.horizontal-display {
  display: flex;
  flex-direction: row;
}

.intro-subheading{
  padding-top: 10;
  font-family: Metropolis-SemiBoldItalic;
  font-size: calc(0.3vh + 1vw);
  background-color: #FD6767;
  border-radius: 12px;
  height: 7.5vmin;
  max-width: 30vw;
  width: 30vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #ffffff;
  letter-spacing: normal;
  padding-right: 2vmin;
  padding-left: 2vmin; 
}

.small-right-spacing {
  padding-right: 1vmin;
}

.small-top-spacing {
  padding-top: 5vmin;
}

.welcome {
  padding-left: 5px;
  align-items: center;
  display: flex;
  justify-content: center;
}